.card__stars {
  display: flex;
}

.card__star {
  background-image: url(../images/star.svg);
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
}

.card__stars--1 .card__star:nth-child(-n + 1),
.card__stars--2 .card__star:nth-child(-n + 2),
.card__stars--3 .card__star:nth-child(-n + 3),
.card__stars--4 .card__star:nth-child(-n + 4),
.card__stars--5 .card__star:nth-child(-n + 5) {
  background-image: url(../images/star-active.svg);
}
